
import Vue from 'vue';
import { Pane } from 'tweakpane';
import { isArray, isObject } from 'lodash';

export interface TweakpaneData {

}

export default Vue.extend({
    name: 'Tweakpane',
    data(): TweakpaneData {
        return {
        }
    },
    props: {
        title: {
            type: String,
            default: 'Parameters'
        }
    },
    mounted() {
        this.pane = new Pane({
            title: this.title,
            container: this.$refs.container as HTMLElement,
        });
    },
    methods: {

    }
});
